.header-nav {
    position: absolute;
    right: 25%;
    .menu-items {
        list-style-type: none;
        display: flex;
        .menu-item li,
        .active li {
            margin-right: 25px;
            font-size: 12px;
            font-family: inherit;
            text-align: center;
            transition: all .6s ease-in-out;
            cursor: pointer;
        }
        .active {
            color: #ffffff;
        }
        .menu-item {
            color: #bcb8da;
            &:hover {
                color: #ffffff !important;
            }
        }
    }
}

.menu-item .icon,
.active .icon {
    font-size: 18px;
    margin-bottom: 4px;
}

.menu-item .icon img {
    width: 100%;
}

@media (max-width: 800px) {
    .menu-items {
        list-style-type: none;
        display: flex;
        justify-content: center;
        width: 100%;
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 99;
        background-color: #0e0f16;
        padding: 10px 0px;
    }
}

@media (max-width: 450px) {
    .header-nav .menu-items .menu-item,
    .header-nav .menu-items .active {
        font-size: 11px;
    }
    .menu-item .icon,
    .active .icon {
        font-size: 16px;
    }
    .header-nav .menu-items .menu-item:not(:last-child),
    .header-nav .menu-items .active:not(:last-child) {
        margin-right: 20px;
    }
    .menu-item .icon,
    .active .icon {
        font-size: 16px;
    }
}

@media (max-width: 380px) {
    .header-nav .menu-items .menu-item,
    .header-nav .menu-items .active {
        font-size: 11px;
    }
    .header-nav .menu-items .menu-item:not(:last-child),
    .header-nav .menu-items .active:not(:last-child) {
        margin-right: 10px;
    }
    .menu-item .icon,
    .active .icon {
        font-size: 14px;
    }
}