@mixin flexColumn {
    display: flex;
    flex-direction: column;
}

.form {
    width: 100%;
    box-sizing: border-box;
    border-radius: 10px;
    padding: 20px 30px 30px;
    background-color: #1e2031;
    display: flex;
    flex-direction: column;
}

input,
textarea,
select {
    background-color: #1a1b2a;
    border: 1px solid #26273d;
    font-family: "Roboto", sans-serif;
    padding: 11px;
    outline: none;
    font-size: 11px;
    color: #b6bad0;
    border-radius: 12px;
    cursor: pointer;
}

.input-data {
    padding: 10px 6px;
    width: 100%;
}

input:focus,
textarea:focus,
select:focus {
    color: #a4aec1;
    background-color: #222331;
    border: 1px solid #484a68;
}

input::placeholder {
    color: #a4aec1;
    font-weight: 300;
}

textarea {
    min-height: 150px;
}

textarea::placeholder {
    color: #8a8ea3;
}

span.input-info {
    color: #8990b5;
    margin: 15px 0 5px;
    font-size: 13px;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
}

.bi-image {
    color: #2421db;
    font-size: 24px;
}

.block {
    @include flexColumn;
}

.main-column {
    display: flex;
    align-items: center;
}

.column1 {
    @include flexColumn;
    flex-grow: 1;
}

.column2 {
    @include flexColumn;
    width: 50%;
}

.column2:not(:last-child),
.column3:not(:last-child) {
    margin-right: 6px;
}

.column3 {
    @include flexColumn;
    width: 33%;
}

.photo-layer {
    padding: 9.2px;
    margin: 0;
    border-radius: 4px;
    background-color: #43359e;
    display: flex;
    width: fit-content;
    align-items: center;
    cursor: pointer;
    transition: all .6s ease-in-out;
    &:hover {
        background-color: #80429f;
    }
}

.photo-icon {
    color: aliceblue;
    margin-right: 5px;
}

.select-photo {
    margin: 0;
    font-family: "Roboto", sans-serif;
    font-size: 11px;
    color: aliceblue;
}

.select-img {
    background-color: #12131c;
    font-family: "Roboto", sans-serif;
    padding: 9px 6px;
    outline: none;
    font-size: 11px;
    color: #8f8f8f;
    border-radius: 4px;
    width: fit-content;
}

.genre {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 7px;
    .item {
        position: relative;
        width: fit-content;
        color: #e0e0e0;
        font-family: "Roboto", sans-serif;
        font-size: 11px;
        display: flex;
        align-items: center;
        padding: 7px;
        border: 1px solid #975A5E;
        border-radius: 4px;
        margin-right: 5px;
        margin-bottom: 5px;
    }
    .close-btn {
        color: #da687b;
        border-radius: 50%;
        cursor: pointer;
        font-size: 15px;
        margin-left: 4px;
    }
}

.datepick {
    width: 100%;
    box-sizing: border-box;
}

span.hide {
    display: none;
}

@media screen and (max-width: 960px) {
    .column2 {
        width: 50%;
    }
    .column3 {
        width: 33%;
    }
}