.container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    max-width: 1320px;
    margin: 0 auto;
}

.page404 {
    width: 100%;
    height: fit-content;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    &::after {
        content: "";
        position: absolute;
        z-index: 99;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(6.67%, rgba(19, 19, 30, .009)), color-stop(13.33%, rgba(19, 19, 30, .036)), color-stop(20%, rgba(19, 19, 30, .082)), color-stop(26.67%, rgba(19, 19, 30, .147)), color-stop(33.33%, rgba(19, 19, 30, .232)), color-stop(40%, rgba(19, 19, 30, .332)), color-stop(46.67%, rgba(19, 19, 30, .443)), color-stop(53.33%, rgba(19, 19, 30, .557)), color-stop(60%, rgba(19, 19, 30, .668)), color-stop(66.67%, rgba(19, 19, 30, .768)), color-stop(73.33%, rgba(19, 19, 30, .853)), color-stop(80%, rgba(19, 19, 30, .918)), color-stop(86.67%, rgba(19, 19, 30, .964)), color-stop(93.33%, rgba(19, 19, 30, .991)), to(#13131e)), -webkit-gradient(linear, left top, right top, color-stop(66.99%, transparent), color-stop(69.19%, rgba(19, 19, 30, .009)), color-stop(71.39%, rgba(19, 19, 30, .036)), color-stop(73.59%, rgba(19, 19, 30, .082)), color-stop(75.79%, rgba(19, 19, 30, .147)), color-stop(77.99%, rgba(19, 19, 30, .232)), color-stop(80.19%, rgba(19, 19, 30, .332)), color-stop(82.4%, rgba(19, 19, 30, .443)), color-stop(84.6%, rgba(19, 19, 30, .557)), color-stop(86.8%, rgba(19, 19, 30, .668)), color-stop(89%, rgba(19, 19, 30, .768)), color-stop(91.2%, rgba(19, 19, 30, .853)), color-stop(93.4%, rgba(19, 19, 30, .918)), color-stop(95.6%, rgba(19, 19, 30, .964)), color-stop(97.8%, rgba(19, 19, 30, .991)), to(#13131e)), -webkit-gradient(linear, right top, left top, color-stop(66.78%, transparent), color-stop(68.99%, rgba(19, 19, 30, .009)), color-stop(71.21%, rgba(19, 19, 30, .036)), color-stop(73.42%, rgba(19, 19, 30, .082)), color-stop(75.64%, rgba(19, 19, 30, .147)), color-stop(77.85%, rgba(19, 19, 30, .232)), color-stop(80.07%, rgba(19, 19, 30, .332)), color-stop(82.28%, rgba(19, 19, 30, .443)), color-stop(84.5%, rgba(19, 19, 30, .557)), color-stop(86.71%, rgba(19, 19, 30, .668)), color-stop(88.93%, rgba(19, 19, 30, .768)), color-stop(91.14%, rgba(19, 19, 30, .853)), color-stop(95.57%, rgba(19, 19, 30, .964)), color-stop(97.79%, rgba(19, 19, 30, .991)), to(#13131e));
    }
    .error {
        display: flex;
        align-items: center;
        flex-direction: column;
        z-index: 99;
        position: absolute;
        text-transform: uppercase;
        opacity: .6;
    }
    .error .number {
        color: #ffffff;
        line-height: 85px;
        font-size: 95px;
        font-family: inherit;
        font-weight: 600;
        text-transform: uppercase;
    }
    .error .text {
        color: rgb(255, 255, 255);
        font-family: inherit;
        font-weight: 300;
        font-size: 17px;
    }
    .video {
        width: 100%;
        max-width: 1700px;
    }
    .vignette {
        position: absolute;
        max-width: 1700px;
        width: 100%;
        height: 100%;
        bottom: 0;
    }
}

.video-layer {
    width: 100%;
    max-width: 1700px;
    height: fit-content;
    display: flex;
    justify-content: center;
    position: relative;
}

@media (max-width: 1000px) {
    .page404 .error .number {
        font-size: 70px;
        line-height: 70px;
    }
    .page404 .error .text {
        font-size: 16px;
    }
}

@media (max-width: 800px) {
    .page404 {
        align-items: flex-end;
    }
    .page404 .video {
        width: 100%;
        max-width: 1600px;
    }
    .page404 .error .number {
        font-size: 80px;
        line-height: 80px;
    }
    .page404 .error .text {
        font-size: 15px;
    }
}

@media (max-width: 450px) {
    .page404 {
        align-items: flex-end;
        height: 300px;
        .error .number {
            font-size: 70px;
            line-height: 70px;
        }
        .error .text {
            font-size: 14px;
        }
        .video {
            width: 100%;
            height: 100%;
            object-fit: cover;
            max-width: 1600px;
        }
    }
}